import { useState, useEffect } from "react";
import {
  Heading,
  Layout,
  Page,
  DisplayText,
  Stack,
  TextStyle,
  IndexTable,
  Card,
  useIndexResourceState,
  Button,
} from "@shopify/polaris";
import CommandSelector from "./Action";
import { HashtagMajor, DeleteMajor } from "@shopify/polaris-icons";
import TaskModal from "./Modal";
import TaskInfo from "./Task";
import axios from "axios";
import { toast } from "react-toastify";

const Task = () => {
  const [active, setActive] = useState(false);
  const [commandList, setCommandList] = useState([]);
  const [update, setUpdate] = useState(false);
  const fetchCommand = () => {
    axios
      .get("/command/")
      .then((res) => {
        let all_commands = res.data.command.map((item) => {
          return (
            <TaskInfo
              key={item.key}
              keyid={item.key}
              device_name={item.device_id}
              command={item.command}
              date={item.date}
              complete={item.success}
              update={update}
              setUpdate={setUpdate}
            />
          );
        });
        setCommandList(all_commands);
      })
      .catch((err) => {
        console.error(err);
      });
  };

  const DeleteAllTask = () => {
    axios
      .get("/command/delete/all")
      .then((res) => {
        toast.success(res.data.message);
        setCommandList([])
      })
      .catch((err) => {
        toast.error("Error Deleting All Tasks");
      });
  };
  useEffect(() => {
    const RunFetchcommand = async () => {
      fetchCommand();
    };
    RunFetchcommand();
  }, [update]);
  return (
    <Page fullWidth>
      <Heading>Task Manager</Heading>
      <br></br>
      <Stack vertical>
        <Stack distribution="equalSpacing">
          <Button primary icon={HashtagMajor} onClick={() => setActive(true)}>
            Run Task
          </Button>
          <Button
            destructive
            icon={DeleteMajor}
            onClick={() => DeleteAllTask()}
          >
            Delele All Task
          </Button>
        </Stack>

        {commandList}
      </Stack>
      <TaskModal
        active={active}
        setActive={setActive}
        fetchCommand={fetchCommand}
      />
    </Page>
  );
};
export default Task;
