import { useState } from "react";
import { Button, Layout, Page, Stack, TextField } from "@shopify/polaris";
import Logo from "../SideNav/2ee9fc9d-72f3-41a9-ae0b-387aa1f6cbdd (1).jpeg";
import { toast } from "react-toastify";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import { login as LoginUser,TOKEN_KEY } from "../utils/auth";

const Login = () => {
  const [username, setusername] = useState("");
  const [password, setpassword] = useState("");
  const navigate = useNavigate();
  const Loginrequest = async () => {
    await axios
      .post("/auth/login", {
        username: username,
        password: password,
      })
      .then(async(res) => {
        await LoginUser(res.data.token);
        axios.defaults.headers.common["Authorization"] = `Bearer ${res.data.token}`;
        toast.success(res.data.message);
      })
      .catch((err) => {
        toast.error(err.response.data.detail);
      });
  };
  return (
    <Page title="Administrator login portal">
      <img src={Logo} width="40%" />
      <Layout>
        <Layout.AnnotatedSection
          title="Bdroid Botnet"
          description="Please change your defualt password ones your login for the first time"
        >
          <Stack vertical={true}>
            <TextField
              label="Username"
              value={username}
              onChange={(data) => setusername(data)}
            ></TextField>
            <TextField
              label="Password"
              value={password}
              onChange={(data) => setpassword(data)}
            ></TextField>
            <Button primary onClick={ async () => await Loginrequest()}>
              Login
            </Button>
          </Stack>
        </Layout.AnnotatedSection>
      </Layout>
    </Page>
  );
};

export default Login;
