import { useEffect, useState } from "react";
import {
  Heading,
  Layout,
  Page,
  DisplayText,
  Stack,
  TextStyle,
  IndexTable,
  Card,
  useIndexResourceState,
  EmptySearchResult,
} from "@shopify/polaris";
import Logo from "./Cyberattack-cuate.svg";
import MapChart from "./map";
import axios from "axios";

const Counter = ({ Title, Count }) => {
  return (
    <Stack vertical={true} alignment="center">
      <DisplayText size="small">{Title}</DisplayText>
      <DisplayText>{Count}</DisplayText>
    </Stack>
  );
};

const DeviceList = ({ client, isloading }) => {
  const resourceName = {
    singular: "client",
    plural: "client",
  };

  const { selectedResources, allResourcesSelected, handleSelectionChange } =
    useIndexResourceState(client);

  const emptyStateMarkup = (
    <EmptySearchResult
      title={"No clients yet"}
      description={"Add a client to get started"}
      withIllustration
    />
  );

  const rowMarkup = client.map(
    (
      {
        key,
        device_name,
        android_version,
        sim_country,
        sim_operator,
        last_online,
      },
      index
    ) => (
      <IndexTable.Row
        id={key}
        key={key}
        selected={selectedResources.includes(key)}
        position={index}
      >
        <IndexTable.Cell>
          <TextStyle variation="strong">{key}</TextStyle>
        </IndexTable.Cell>
        <IndexTable.Cell>{device_name}</IndexTable.Cell>
        <IndexTable.Cell>{android_version}</IndexTable.Cell>
        <IndexTable.Cell>{sim_country}</IndexTable.Cell>
        <IndexTable.Cell>{sim_operator}</IndexTable.Cell>
        <IndexTable.Cell>{last_online}</IndexTable.Cell>
      </IndexTable.Row>
    )
  );

  return (
    <Card>
      <IndexTable
        resourceName={resourceName}
        itemCount={client.length}
        emptyState={emptyStateMarkup}
        selectedItemsCount={
          allResourcesSelected ? "All" : selectedResources.length
        }
        onSelectionChange={handleSelectionChange}
        headings={[
          { title: "key" },
          { title: "Device name" },
          { title: "Android version" },
          { title: "Country" },
          { title: "Sim operator" },
          { title: "Last online" },
        ]}
        selectable={false}
        hasMoreItems={false}
        condensed={false}
        hasMoreItems={true}
        loading={isloading}
      >
        {rowMarkup}
      </IndexTable>
    </Card>
  );
};

const Home = () => {
  const [client, setClient] = useState([]);
  const [isloading, setIsLoading] = useState(true);
  const FetchClient = async () => {
    await axios.get("/client/").then((res) => {
      setClient(res.data.clients);
    });
    setIsLoading(false);
  };
  useEffect(() => {
    const runfetch = async () => {
      await FetchClient();
    };
    runfetch();
  }, []);
  return (
    <Page fullWidth>
      <Heading>Overview</Heading>
      <Layout>
        <Layout.Section oneHalf>
          <img width="70%" src={Logo} />
        </Layout.Section>
        <Layout.Section oneHalf>
          <MapChart />
        </Layout.Section>
      </Layout>
      <DeviceList client={client} isloading={isloading} />
    </Page>
  );
};
export default Home;
