import enTranslations from "@shopify/polaris/locales/en.json";
import { AppProvider } from "@shopify/polaris";
import Navbar from "./components/SideNav/Nav";
import {
  Routes,
  Route,
  Navigate,
  Link as ReactRouterLink,
} from "react-router-dom";
import "./App.css";
import Home from "./components/Home";
import Task from "./components/Task";
import Notification from "./components/Notification";
import { ToastContainer } from "react-toastify";
import Login from "./components/Login";
import { isLogin } from "./components/utils/auth";

function App() {

  return (
    <AppProvider
      i18n={enTranslations}
      linkComponent={Link}
      theme={{
        colorScheme: "dark",
      }}
    >
      <Routes>
        <Route
          path="/v4/overview"
          element={
            isLogin() ? (
              <Navbar actualPageMarkup={<Home />} Location={"/overview"} />
            ) : (
              <Navigate to="/v4/login" />
            )
          }
          exact
        />
        <Route
          path="/v4/task"
          element={
            isLogin() ? (
              <Navbar actualPageMarkup={<Task />} Location={"/task"} />
            ) : (
              <Navigate to="/v4/login" />
            )
          }
          exact
        />
        <Route
          path="/v4/notification"
          element={
            isLogin() ? (
              <Navbar
                actualPageMarkup={<Notification />}
                Location={"/notification"}
              />
            ) : (
              <Navigate to="/v4/login" />
            )
          }
          exact
        />

        <Route path="/v4/login" element={<Login />} exact />
        <Route path="/v4/" element={<Login />} exact />

        {/* <Route path="*" element={<NoMatch />} /> */}
      </Routes>
      <ToastContainer
        position="bottom-center"
        autoClose={5000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
        theme="dark"
      />
    </AppProvider>
  );
}

function Link({ children, url = "", ...rest }) {
  // Use an regular a tag for external and download links
  if (isOutboundLink(url) || rest.download) {
    return (
      <a href={url} {...rest}>
        {children}
      </a>
    );
  }

  return (
    <ReactRouterLink to={url} {...rest}>
      {children}
    </ReactRouterLink>
  );
}

function isOutboundLink(url) {
  return /^(?:[a-z][a-z\d+.-]*:|\/\/)/.test(url);
}

export default App;
