import { useState, useCallback } from "react";
import {
  Card,
  Stack,
  Button,
  Collapsible,
  TextContainer,
  DataTable,
  Icon,
  TextStyle,
} from "@shopify/polaris";
import DataModel from "./DataModel";
import {
  DeleteMajor,
  PlayMajor,
  HashtagMajor,
  MobileAcceptMajor,
  MobileCancelMajor,
} from "@shopify/polaris-icons";
import { toast } from "react-toastify";
import { useNavigate } from "react-router-dom";
import axios from "axios";

const TaskInfo = ({
  keyid,
  device_name,
  command,
  date,
  complete,
  update,
  setUpdate,
}) => {
  const [active, setActive] = useState(false);
  const handleToggle = useCallback(() => setActive((active) => !active), []);
  const navigate = useNavigate();
  const handlesuccess = async () => {
    if (
      command === "changewallpaper" ||
      command === "makecall" ||
      command === "sendsms"
    ) {
      toast.success("Task Executed Successfully");
    } else if (command === "getlocation") {
      await axios.get(`/command/response/${keyid}`).then((res) => {
        let response = res.data.response;
        window.open(
          `https://maps.google.com/?q=${response.latitude},${response.longitude}`
        );
      });
    } else if (command === "getfile") {
      await axios.get(`/command/response/${keyid}`).then((res) => {
        let response = res.data.response;
        window.open(
          window.location.protocol +
            "//" +
            window.location.hostname +
            ":" +
            window.location.port +
            "/command/download/" +
            response
        );
      });
    } else {
      handleToggle();
    }
  };

  const DeleteTask = async () => {
    axios
      .get(`/command/delete/id/${keyid}`)
      .then((res) => {
        toast.success(res.data.message);
        setUpdate(!update);
      })
      .catch((err) => {
        toast.error("Error Deleting Task");
      });
  };

  const ShowError = async () => {
    toast.error("There was an error");
  };
  return (
    <Card>
      <div style={{ padding: "10px" }}>
        <Stack vertical={false} distribution="equalSpacing">
          <Stack spacing="extraTight">
            <Icon source={HashtagMajor} color="highlight" />
            <TextStyle variation="strong">Key : {keyid}</TextStyle>
          </Stack>
          <TextStyle variation="strong">Device id : {device_name}</TextStyle>
          <TextStyle variation="strong">Command : {command}</TextStyle>
          <TextStyle variation="strong">Date : {date}</TextStyle>
          <Stack spacing="extraTight">
            <TextStyle variation="strong">Complete : </TextStyle>
            <Icon
              source={complete ? MobileAcceptMajor : MobileCancelMajor}
              color="highlight"
            />
          </Stack>
          <div
            onClick={async () => await DeleteTask()}
            style={{ cursor: "pointer" }}
          >
            <Icon source={DeleteMajor} color="highlight" />
          </div>
          <div
            onClick={async () =>
              complete ? await handlesuccess() : await ShowError()
            }
            style={{ cursor: "pointer" }}
          >
            <Icon source={PlayMajor} color="highlight" />
          </div>
        </Stack>
      </div>
      <DataModel
        active={active}
        setActive={setActive}
        keyid={keyid}
        command={command}
        success={complete}
      />
    </Card>
  );
};

export default TaskInfo;
