import { useState, useCallback, useEffect } from "react";
import {
  Modal,
  Button,
  Stack,
  TextContainer,
  Checkbox,
  DataTable,
  TextStyle,
} from "@shopify/polaris";
import axios from "axios";

const TaskModal = ({ active, setActive, keyid, command }) => {
  const toggleActive = useCallback(() => setActive((active) => !active), []);
  const [header, setHeader] = useState([]);
  const [response, setResponse] = useState([]);
  const getResponse = () => {
    axios
      .get(`command/response/${keyid}`)
      .then((res) => {
        if (res.data.success) {
          setHeader(res.data.header);
          setResponse(res.data.response);
        }
      })
      .catch((err) => {
        console.error(err);
      });
  };

  useEffect(() => {
    getResponse();
  }, [keyid, active]);
  if (command === "runshell" || command === "getservices" || command === "getapps") {
    return (
      <Modal
        large
        open={active}
        onClose={toggleActive}
        title="Bdroid Console"
      >
        <TextContainer>
          {response.map((item) => (
            <TextStyle variation="subdued">
              {item}
              <br></br>
            </TextStyle>
          ))}
        </TextContainer>
      </Modal>
    );
  } else if (command === "listfile") {
    return (
      <Modal
        large
        open={active}
        onClose={toggleActive}
        title="Bdroid Console"
      >
        <TextContainer>
          {response !== undefined
            ? response.map((item) => (
                <TextStyle variation="subdued">
                  {item}
                  <br></br>
                </TextStyle>
              ))
            : "No file found"}
        </TextContainer>
      </Modal>
    );
  } else if (command === "getcontact") {
    return (
      <Modal
        open={active}
        onClose={toggleActive}
        title="Bdroid Console"
      >
        <TextContainer>
          {response !== undefined
            ? response.map((item) => (
                <TextStyle variation="subdued">
                  {console.log(item[0] + "  :  " + item[1])}
                  {item[0] + ": " + item[1]}
                  <br></br>
                </TextStyle>
              ))
            : "No file found"}
        </TextContainer>
      </Modal>
    );
  } else {
    return (
      <div style={{ overflow: "hidden" }}>
        <Modal
          large
          open={active}
          onClose={toggleActive}
          title="Bdroid Console"
        >
          <DataTable
            columnContentTypes={
              header !== undefined ? header.map((item) => "text") : []
            }
            headings={header}
            rows={response}
            hideScrollIndicator={false}
          />
        </Modal>
      </div>
    );
  }
};

export default TaskModal;
