import { useState, useEffect } from "react";
import {
  Heading,
  Layout,
  Page,
  DataTable,
  Stack,
  TextStyle,
  IndexTable,
  Card,
  useIndexResourceState,
  Button,
} from "@shopify/polaris";
import axios from "axios";
import User from "./User";
import { toast } from "react-toastify";

const Notification = () => {
  const [user, setUser] = useState("");
  const [notification, setnotification] = useState([]);
  const FetchNotification = () => {
    if (user !== "") {
      axios
        .get(`/notification/device/${user}`)
        .then((res) => {
          setnotification(res.data.notification);
        })
        .catch((err) => {
          console.error(err);
        });
    }
  };

  useEffect(() => {
    FetchNotification();
  }, [user]);
  const DeleteNotification = () => {
    axios
      .get(`/notification/delete/${user}`)
      .then((res) => {
        toast.success(res.data.message);
      })
      .catch((err) => {
        toast.error("Error Deleting All Notification");
      });
    FetchNotification();
  };
  return (
    <Page fullWidth>
      <Heading>Notification</Heading>
      <br></br>
      <Stack distribution="equalSpacing">
        <User selected={user} setSelected={setUser} />
        <Button destructive onClick={() => DeleteNotification()}>
          Delete All Notification
        </Button>
      </Stack>

      <Card>
        <DataTable
          columnContentTypes={["text", "text", "text", "text", "text"]}
          headings={[
            "Package",
            "Date",
            "Device_id",
            "Notification Body",
            "Title Text",
          ]}
          rows={notification}
        />
      </Card>
    </Page>
  );
};
export default Notification;
