import { useState } from "react";
import {
  Stack,
  TextStyle,
  Subheading,
  Modal,
  TextContainer,
  TextField,
} from "@shopify/polaris";
import axios from "axios";
import { toast } from "react-toastify";
import { logout } from "../utils/auth";

const Footer = () => {
  const [active, setActive] = useState(false);
  const [oldpassword, setoldpassword] = useState("");
  const [newpassword, setnewpassword] = useState("");
  const ChangePassword = () => {
    axios
      .post("/auth/password/change", {
        old_password: oldpassword,
        new_password: newpassword,
      })
      .then((res) => {
        toast.success(res.data.message);
        setActive(false);
      })
      .catch((err) => {
        toast.error(err.response.data.detail);
      });
  };
  return (
    <div style={{ marginTop: "auto" }}>
      <a
        onClick={() => (window.location.href = "https://contact-us-hacksec.deta.dev/contactus/")}
        style={{ textDecoration: "none", cursor: "pointer" }}
      >
        <Subheading>
          <TextStyle variation="subdued">Contact us</TextStyle>
        </Subheading>
      </a>
      <a
        onClick={() => logout()}
        style={{ textDecoration: "none", cursor: "pointer" }}
      >
        <Subheading>
          <TextStyle variation="subdued">Logout</TextStyle>
        </Subheading>
      </a>
      <a
        onClick={() => setActive(true)}
        style={{ textDecoration: "none", cursor: "pointer" }}
      >
        <Subheading>
          <TextStyle variation="subdued">Change password</TextStyle>
        </Subheading>
      </a>
      <Subheading>
        <TextStyle variation="subdued">Bdroid Botnet</TextStyle>
      </Subheading>
      <Subheading>
        <TextStyle variation="subdued">Made in India with </TextStyle>
      </Subheading>
      <Modal
        open={active}
        onClose={() => setActive(false)}
        title="Change password"
        primaryAction={{
          content: "Change password",
          onAction: ChangePassword,
        }}
      >
        <div style={{ padding: "10px" }}>
          <Stack vertical={true}>
            <TextField
              label="Old password : "
              value={oldpassword}
              onChange={(data) => setoldpassword(data)}
              autoComplete="on"
            ></TextField>
            <TextField
              label="New password : "
              value={newpassword}
              onChange={(data) => setnewpassword(data)}
              autoComplete="on"
            ></TextField>
          </Stack>
        </div>
      </Modal>
    </div>
  );
};

export default Footer;
